import {StaticMethods, Format} from './StaticMethods';
import Token from "../Token";
import Url from '../ServerUrl';
import axios from 'axios';

interface show{
    page : number,
    per_page : number,
}

class Logs extends StaticMethods{

    static async show(logs:show){
        
        let format:Format = {
            network_error : false,
            status        : 0,
            data          : '',
        }
        
        if(!Token.exist()){
            format.status = 401;
            format.data = 'Cookies Not Found';
            return format;
        }
        
        const header = super.header(Token.get());
        
        await axios({
            method  :   "GET",
            url     :   Url.logs+'?page='+logs.page+'&per_page='+logs.per_page,
            headers :   header,
        })
        // .then( this.sleeper(1000))
        .then( (response:any) => {
            format.status = response.status;
            format.data = response.data;
        }).catch( async (error) =>{
            format = await super.recall({
                error : error,
                callback : this.show,
                format : format,
                params : logs
            });
        })
        return format;
    }
}

export default Logs;